import React, { useState, useEffect } from 'react';

import ArticleListComponent from '../components/ArticlesListComponent';
import CommentsListComponent from '../components/CommentsListComponent';
import UpvoteSectionComponent from '../components/UpvoteSectionComponent';
import AddCommentFormComponent from '../components/AddCommentFormComponent';
import NotFoundPage from './NotFoundPage';

import articleContent from './article-content';

const ArticlePage = ({match}) => {

    const name = match.params.name;

    // This is our state. So whenever you are using function syntax we can use the hooks as below.
    // In thius case the articleInfo state is being managed by the its corresposnding method which si setArticleInfo
    const [articleInfo, setArticleInfo] = useState({name: '', upvotes:0 , comments: []  });

    /**
     * gets called whenver the name values changes. 
     * This is done because we onyl need to change values whenever the articles changes.
     */
    useEffect(() => {

        const fetchData = async () => {
            const results = await fetch(`/api/articles/${name}`);
            
            const body = await results.json();
            
            setArticleInfo(body);
        }
        fetchData();
    }, [name]);

    // gathering article details from static content page
    const article = articleContent.filter(article => article.name === name)[0];
    // if the article not found we return not found page
    if(!article) <NotFoundPage />

    // gathering all the other articles.
    const otherArticles = articleContent.filter(article => article.name !== name);

    return(
        <>
        <h1>{article.title}</h1>
        <UpvoteSectionComponent articleName={articleInfo.name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
        { article.content.map((paragraph,key) => (
            <p key={key} >{paragraph}</p>
        ))}

        <AddCommentFormComponent articleName={articleInfo.name} setArticleInfo={setArticleInfo} />
        <CommentsListComponent comments={articleInfo.comments} />

        <h2>Other Articles:</h2>
        <ArticleListComponent articles={otherArticles} />
        </>
    );
}

export default ArticlePage;